				deliveryTime: 1599619299000,
				estimateTime: 1599619299000,
				createTime: 1599619299000,
				key: ['减速机：邦利飞', '变速器：GJB', '油泵：潍柴', '柱塞马达：力士乐'],
				node: '生产',
				secondNode: '装配',
				recordOpen: true,
				record: [{
					name: '张三',
					content: '怎么还没有进度？',
